<template>
	<div class="upload" v-if="claimStatus!==undefined">
		<div class="back" v-if="$route.query.fromClaimApply" @click="goClaimApply"><img src="../../assets/images/icon_right.png"
			 alt=""></div>
		<template v-if="claimStatus==1">
			<div class="upload">
				<div class="ctx">
					<div class="msg">
						<div class="order_status clearfix">
							<div class="order fl">
								理赔申请编号：{{data.sn}}
							</div>
							<div class="status fr">
								{{data.status | claimStatus}}
							</div>
						</div>
						<div class="name">
							{{data.refer_name}}
						</div>
					</div>
					<div class="tip">
						<div class="ttl">
							您需要准备以下材料至离您最近的
							<span style="color: #000000;font-weight: 600;">{{companyName}}</span>
							营业厅办理理赔资料提交
							<template v-if="CSH">
								,获取最近营业厅或咨询其他问题拨打安联财险的客服热线：{{CSH}}。
							</template>
							<template v-else>：</template>
						</div>
						<p v-for="(item,index) in recordDatas" class='record-item'>
							{{`${index+1}${item.name}`}}
						</p>
					</div>
				</div>
			</div>
		</template>
		<template v-else>
			<div class="ctx">
				<div class="msg">
					<div class="order_status clearfix">
						<div class="order fl">
							理赔申请编号：{{data.sn}}
						</div>
						<div class="status fr">
							{{data.status | claimStatus}}
						</div>
					</div>
					<div class="name">
						{{data.refer_name}}
					</div>
				</div>
				<div class="up-list">
					<div class="up-item" v-for="(item,index) in data.claim_record_data">
						<div class="title">{{`${index+1}.${item.name}${item.isNecessary ? '(可选)':''}`}}
							<img class="i-help fr" v-if="item.status=='unpassed'" src="@/assets/images/icon_explain.png" @click="handleShowReason(index)">
							<span class="status fr" v-if="item.status=='passed'||item.status=='unpassed'" :class="{unpassed: item.status=='unpassed'}">{{item.status | recordStatus}}</span>
							<div class="reason" :class="{show_reason: show_reason[index]}">
								{{item.reason}}
							</div>
						</div>
						<div class="ttl">{{item.tip}}</div>
						<div class="img-list clearfix">
							<div class="img-item" v-if="item.url">
								<img :src="getImgUrl(item)" @load='imgStyle($event)' @click="show_Bigimg(item)" />
								<span class="deleteImg" @click="handledeleteImg(item)" v-if="item.status!='passed'"></span>
							</div>
							<div class="img-item nullImg" @click="showCamera(item)" v-else></div>
						</div>
						<div class="billNumber" v-if="item.url && item.isBillNumber">
							<span v-if="item.billNumber">业务流水号：{{item.billNumber}}</span>
							<span class="red" v-else>未填写业务流水号</span>
							<span class="fr btn" @click="showBigimg(item)">{{item.billNumber ? '修改' : '填写'}}</span>
						</div>
					</div>
				</div>
			</div>
			<UserMedia :show='camera' @hide='hideCamera' @handlechangeimg='handleChange'></UserMedia>
			<div class="fullScreen" v-if="fullImg.show" @click.self="closeFullImg">
				<mHeader :title="'修改业务流水号'"></mHeader>
				<div class="returnBtn" @click="$router.back()"></div>
				<img :src="getImgUrl(fullImg)" @load='_imgStyle($event)' @click.self="closeFullImg">
				<div class="billinput" v-if="fullImg.isBillNumber"><span class="txt">业务流水号：</span><input type="text" v-model="fullImg.billNumber" />
					<span class="ok" @click="handlechangebill">确定</span>
				</div>
			</div>
			<div class="fullScreen" v-if="full_Img.show" @click="_closeFullImg">
				<img :src="getImgUrl(full_Img)" @load='_imgStyle($event)'>
			</div>
		</template>
		<div class="foot">
			<div v-if="claimStatus==1" class="okbtn" @click="toClaimList">确 定</div>
			<template v-else>
				<div class="fbtn save" @click="saveAndSubmit(0)">保存</div>
				<div class="fbtn up" @click="saveAndSubmit(1)">提交</div>
			</template>
		</div>
		<LoadAnimate v-if='loading'></LoadAnimate>
	</div>
</template>

<script>
	import mHeader from '@/components/base/mHeader.vue'
	import axios from 'axios'
	import UserMedia from '@/components/claim/userMedia.vue'
	import LoadAnimate from '@/components/base/LoadAnimate.vue'
	window.data_copy = {}
	export default {
		data() {
			return {
				camera: false,
				data: {},
				activeFileWrap: null, // 最近被点击的图片添加框
				files: [],
				fullImg: {},
				// 单纯展示图片
				full_Img: {},
				show_reason: {},
				loading: false,
				orderSn: '',
				groupOrderSn: '',
				//提交类型
				claimStatus: undefined,
				recordDatas: [],
				CSH: '',
				companyName: ''
			}
		},
		methods: {
			goClaimApply() {
				this.$router.replace({
					path: '/claimlist/claimApply',
					query: {
						orderSn: this.$route.query.orderSn,
						type: this.$route.query.type,
						sn: this.$route.query.claimApplySn
					}
				})
			},
			toClaimList() {
				this.$router.push('/claimlist')
			},
			hideCamera() {
				this.camera = false
			},
			showCamera(item) {
				this.activeFileWrap = item
				this.camera = true
			},
			handleChange(file) {
				let URL = window.URL || window.webkitURL
				this.activeFileWrap.url = URL.createObjectURL(file)
				data_copy.claim_record_data.find((c) => {
					return c.claimDataConfigId == this.activeFileWrap.claimDataConfigId
				}).url = URL.createObjectURL(file)
				this.data.claim_record_data.find((c) => {
					return c.claimDataConfigId == this.activeFileWrap.claimDataConfigId
				}).status = 'uploaded'
				data_copy.claim_record_data.find((c) => {
					return c.claimDataConfigId == this.activeFileWrap.claimDataConfigId
				}).status = 'uploaded'
				this.files.push({
					claimDataConfigId: this.activeFileWrap.claimDataConfigId,
					file: file
				})
			},
			handledeleteImg(item) {
				item.url = null
				let claim_record_data_item = data_copy.claim_record_data.find((cla, index) => cla.claimDataConfigId == item.claimDataConfigId)
				claim_record_data_item.url = ''
				if (claim_record_data_item.billNumber) {
					claim_record_data_item.billNumber = ''
					this.data.claim_record_data.find((cla, index) => cla.claimDataConfigId == item.claimDataConfigId).billNumber = ''
				}
				this.files.splice(this.files.findIndex((file, index) => file.claimDataConfigId == item.claimDataConfigId), 1)
			},
			// 尺寸
			imgStyle(event) {
				let img = event.target
				let shape = img.naturalWidth / img.naturalHeight
				if (shape > 1) {
					img.style.width = '100%'
					img.style.height = 'auto'
				} else {
					img.style.height = '100%'
					img.style.width = 'auto'
				}
			},
			// 大屏尺寸
			_imgStyle(event) {
				let img = event.target
				let shape = img.naturalWidth / img.naturalHeight
				if (shape > document.documentElement.clientWidth / document.documentElement.clientHeight) {
					img.style.width = '100%'
					img.style.height = 'auto'
				} else {
					img.style.height = '100%'
					img.style.width = 'auto'
				}
			},
			getImgUrl(item) {
				return item.url.indexOf('blob:http') > -1 ? item.url : process.env.VUE_APP_RESOURCE_URL + item.url
			},
			showBigimg(item) {
				this.fullImg = {
					show: true,
					url: item.url,
					claimDataConfigId: item.claimDataConfigId,
					billNumber: item.billNumber,
					isBillNumber: item.isBillNumber
				}
			},
			show_Bigimg(item) {
				this.full_Img = {
					show: true,
					url: item.url
				}
			},
			closeFullImg() {
				this.fullImg = {}
			},
			_closeFullImg() {
				this.full_Img = {}
			},
			handlechangebill() {
				data_copy.claim_record_data.find((claim, index) => claim.claimDataConfigId == this.fullImg.claimDataConfigId)
					.billNumber = this.fullImg.billNumber
				this.data.claim_record_data.find((claim, index) => claim.claimDataConfigId == this.fullImg.claimDataConfigId)
					.billNumber = this.fullImg.billNumber
				this.closeFullImg()
			},
			handleShowReason(index) {
				this.$set(this.show_reason, index, !this.show_reason[index])
			},
			upload() {
				this.loading = true;
				return new Promise((resolve, reject) => {
					this.files.forEach((file, index) => {
						let formData = new FormData()
						formData.append('file', file.file)
						axios.post('/common/file/upload',
								formData, {
									headers: {
										'Content-Type': 'multipart/form-data'
									}
								})
							.then(res => {
								let data = res.data
								data_copy.claim_record_data.find(c => file.claimDataConfigId == c.claimDataConfigId).url = (data.data &&
									data.data.filePath) || ''
								if (index + 1 == this.files.length) {
									this.loading = false;
									resolve()
								}
							})
							.catch((e) => {
								reject(e);
								this.loading = false;
							})
					})
					if (!this.files.length) {
						this.loading = false;
						resolve()
					}
				})
			},
			saveAndSubmit(status) {
				if (status == 1 && !this.verify()) return
				this.upload().then(() => {
					this.$api.cla.submit({
						claimRecordId: data_copy.id,
						claimRecordDatas: JSON.stringify(data_copy.claim_record_data),
						status: status
					}).then((res) => {
						this.$toast.center(res.message)
						if (status == 1 && res.code == 1) {
							this.$router.push('/claimUpload/success/' + data.sn)
						}
					})
				})
			},
			verify() {
				let res = true
				data_copy.claim_record_data.forEach((claim, i) => {
					// isNecessary代表非必需 !isNecessary是必填项
					if (!claim.isNecessary) {
						if (!claim.url) {
							res = false
							this.$toast.center('请先上传完整资料后再提交')
						}
					}
				})
				res && data_copy.claim_record_data.forEach((claim, i) => {
					if (claim.url && !!claim.isBillNumber && !claim.billNumber) {
						res = false
						this.$toast.center('请先填写票据业务流水号')
					}
				})
				return res
			}
		},
		components: {
			UserMedia,
			mHeader,
			LoadAnimate
		},
		mounted() {
			if (this.$route.query.type == 0) {
				// 个险
				this.orderSn = this.$route.query.orderSn
			}
			if (this.$route.query.type && this.$route.query.type == 1) {
				// 团险
				console.log(7777, this.$route.query)
				this.groupOrderSn = this.$route.query.groupOrderSn
				console.log(999, this.groupOrderSn)
			}
		},
		created() {
			this.$api.claim.claimStatus({
				sn: this.$route.query.claimApplySn
			}).then((res) => {
				console.log('console', res.data)
				if (res.code == 1) {
					this.claimStatus = res.data;
					this.$api.cla.getRecord({
						sn: this.$route.query.claimApplySn
					}).then((res) => {
						if (res.code == 1) {
							this.data = JSON.parse(JSON.stringify(res.data || '[]'))
							data_copy = JSON.parse(JSON.stringify(res.data || '[]'))
						} else {
							console.error('getData fail')
						}
					})
					if (res.data == 1) { //柜
						this.$api.claim.getCounterMsg({
							sn: this.$route.query.claimApplySn
						}).then((res) => {
							if (res.code == 1) {
								console.log('console', JSON.parse(res.recordDatas), res.CSH, res.companyName);
								this.recordDatas = JSON.parse(res.recordDatas);
								this.CSH = res.CSH;
								this.companyName = res.companyName;
							}
						})
					}
				}
			})
		}
	}
</script>

